html {
  height: 100%;
}

body {
  min-width: 320px;
  height: 100%;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  background-color: aliceblue;
}

.visually-hidden {
  position: absolute;
  width: 0;
  height: 0;
  margin: 0;
  padding: 0;
  overflow: hidden;
  border: none;
  outline: 0;
}

.header {
  padding: 15px;
}

.container {
  position: relative;
  display: flex;
  max-width: 990px;
  margin-right: auto;
  margin-left: auto;
  padding: 15px;
  background-color: #fff;
  border-radius: 25px;
  gap: 10px;
}

.logo {
  display: block;
  width: fit-content;
  height: fit-content;
  text-decoration: none;
}

.logo img {
  display: block;
  width: 40px;
  height: 40px;
}

.nav {
  margin-left: auto;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.nav__list a {
  display: block;
  padding: 9px;
  font-size: 18px;
  color: inherit;
  text-decoration: none;
  transition: opacity 0.25s ease-in-out;
}

.nav__list a:hover {
  opacity: 0.7;
}

.nav__list a:active {
  opacity: 0.4;
}

@media (min-width: 600px) {
  .nav__toggle {
    display: none;
  }

  .nav__list {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
  }
}

@media (max-width: 600px) {
  .nav__toggle {
    position: relative;
    display: block;
    width: 40px;
    height: 40px;
    padding: 0;
    background-color: transparent;
    border: 0;
    cursor: pointer;
    transition: opacity 0.25s ease-in-out;
    appearance: none;
  }

  .nav__toggle:hover {
    opacity: 0.7;
  }

  .nav__toggle:active {
    opacity: 0.4;
  }

  .nav__toggle::after,
  .nav__toggle::before {
    content: "";
    position: absolute;
    left: 50%;
    width: 30px;
    height: 2px;
    background-color: #000000;
    transform: translateX(-50%);
    transform-origin: center;
    transition: top 0.25s ease-in-out, transform 0.25s ease-in-out, box-shadow 0.25s ease-in-out;
  }

  .nav__toggle::before {
    top: 7px;
    box-shadow: 0 12px 0 #000000;
  }

  .nav__toggle::after {
    bottom: 7px;
  }

  .nav__list {
    position: absolute;
    top: calc(100% + 15px);
    left: 50%;
    z-index: 1;
    display: grid;
    box-sizing: border-box;
    width: 290px;
    padding: 15px;
    background-color: #ffffff;
    border-radius: 25px;
    transform: translate(-50%);
    gap: 5px;
  }

  .nav__list {
    visibility: hidden;
    opacity: 0;
  }

  .nav--no-js:focus-within .nav__list,
  .nav--open .nav__list {
    visibility: visible;
    opacity: 1;
    transition: visibility 0.25s ease-in-out, opacity 0.25s ease-in-out;
  }

  .nav--open .nav__toggle::before,
  .nav--open .nav__toggle::after {
    top: 50%;
  }

  .nav--open .nav__toggle::before {
    box-shadow: 0 0 0 transparent;
    transform: translate(-50%, -50%) rotate(45deg);
  }

  .nav--open .nav__toggle::after {
    transform: translate(-50%, -50%) rotate(-45deg);
  }
}